.hero {
    /* height: 100vh; */
    background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);;
    display: flex;
    justify-content: space-around;
}
.hero-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
}
.hero-left h2 {
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}
.hero-left p {
    color: #171717;
    font-size: 75px;
    font-weight: 700;
}
.hero-hand-icon {
    display: flex;
    align-items: center;
    gap: 20px;
}
.hero-hand-icon img {
    width: 105px;
}
.hero-latest-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
}

.hero-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 200px;
}
.hero-right img {
    height: 90%;
}

@media(max-width:1024px){
    .hero-left{
        padding-left: 80px;
    }
    .hero-left h2 {
        font-size: 20px;
    }
    .hero-left p {
        font-size: 50px;
    }
    .hero-hand-icon img {
        width: 65px;
    }
    .hero-latest-icon {
        width: 220px;
        height: 50px;
        font-size: 16px;
    }
    .hero-right img {
        width: 400px;
    }
}
@media(max-width:800px){
    .hero-left{
        padding-left: 30px;
    }
    .hero-left h2 {
        font-size: 16px;
    }
    .hero-left p {
        font-size: 40px;
    }
    .hero-hand-icon img {
        width: 50px;
    }
    .hero-latest-icon {
        width: 175px;
        height: 40px;
        font-size: 16px;
    }
    .hero-right img {
        width: 300px;
    }
}
@media(max-width:500px){
    .hero {
        flex-direction: column;
        height: 55vh;
    }
    .hero-right {
        display: none;
    }
    .hero-left h2 {
        font-size: 14px;
    }
    .hero-left p {
        font-size: 50px;
    }
    .hero-latest-icon {
        width: 200px;
        height: 45px;
        font-size: 14px;
    }
}
