.popular {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* gap: 10px; */
}
.popular h1 {
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.popular hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.popular-item {
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
    /* gap: 5px; */
    margin-bottom: 50px;
}
@media(max-width:1024px) {
    .popular{
        height: 60vh;
    }
    .popular h2 {
        font-size: 30px;
    }
    .popular hr {
        width: 120px;
        height: 3px;
    }
    .popular-item {
        gap: 15px;
        margin-top: 20px;
    }
}
@media(max-width:800px) {
    .popular{
        height: 50vh;
    }
    .popular h1 {
        font-size: 20px;
    }
    .popular hr {
        width: 100px;
    }
    .popular-item {
        gap: 5px;
        /* smargin-top: 20px; */
    }
}
@media(max-width:500px) {
    .popular{
        height: 90vh;
    }
    .popular-item {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
}