.news-letter {
    width: 65%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    margin-top: 100px;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
    gap: 30px;
}
.news-letter h2 {
    color: #454545;
    font-size: 50px;
    font-weight: 600;
    padding-top: 50px;
}
.news-letter p {
    color: #454545;
    font-size: 20px;
}
.news-letter div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid #e3e3e3;
}
.news-letter input {
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;
}

.news-letter div button {
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}
@media(max-width:1024px){
    .news-letter{
        margin-bottom: 60px;
    }
    .news-letter h2 {
        font-size: 30px;
    }
    .news-letter p {
        font-size: 14px;
    }
    .news-letter div {
        width: 500px;
        height: 50px;
    }
    .news-letter div input {
        width: 300px;
        font-size: 14px;
    }
    .news-letter div button {
        width: 120px;
        height: 50px;
        font-size: 14px;
    }
}

@media(max-width:800px){
    .news-letter{
        margin-bottom: 30px;
    }
    .news-letter h2 {
        font-size: 18px;
    }
    .news-letter p {
        font-size: 12px;
    }
    .news-letter div {
        width: 400px;
        height: 40px;
    }
    .news-letter div input {
        width: 200px;
        font-size: 13px;
    }
    .news-letter div button {
        width: 100px;
        height: 40px;
        font-size: 13px;
    }
}
@media(max-width:500px){
    .news-letter{
        width: 100%;
        padding: 0;
        gap: 15px;
    }
    .news-letter h2 {
        font-size: 16px;
    }
    .news-letter p {
        font-size: 13px;
        max-width: 200px;
        text-align: center;
    }
    .news-letter div {
        width: 290px;
    }
    .news-letter div input {
        width: 130px;
    }
    .news-letter div button {
        width: 100px;
        height: 40px;
    }
}